import { HeroImage, renderGQLComponents } from '@/cmsComponents';
import { Navigation, WidthBoundary } from '@/components';

import { colors } from '@/theme';
import { ProductPage } from '@/types/cmsModels';
import styled from '@emotion/styled';
import { graphql } from 'gatsby';

interface Props {
  data: {
    contentfulPage: ProductPage;
  };
  location: Location;
}

const { Container, ComponentContainer, ComponentHeader, Divider } = {
  Container: styled.div`
    padding-top: 16px;
    padding-bottom: 16px;
    width: 100%;
    min-height: 100%;
    background-color: ghostwhite;
  `,
  ComponentContainer: styled.div`
    margin-bottom: 24px;
  `,
  ComponentHeader: styled.h3`
    padding-bottom: 4px;
    padding-left: 16px;
    margin: 0;
    text-transform: capitalize;
  `,
  Divider: styled.div`
    background-image: linear-gradient(
      ${colors.textDefault},
      ${colors.textDefault}
    );
    background-repeat: repeat-x;
    background-size: 1px 1px;
    background-position: 0 100%;
    height: 1px;
  `,
};

export default function Components({ data, location }: Props) {
  const { hero, components } = data.contentfulPage;
  const renderComponents = renderGQLComponents(components)?.map(
    (component) => ({ title: `temp`, component }),
  );
  return (
    <Container>
      <Navigation location={location} transparent={!!hero} />
      {hero && <HeroImage image={hero.image.gatsbyImageData} />}

      <WidthBoundary>
        <h6>Beschikbare Componenten</h6>
      </WidthBoundary>
      {renderComponents &&
        renderComponents.map(({ title, component }) => (
          <ComponentContainer key={title}>
            <WidthBoundary backgroundStyle={undefined}>
              <ComponentHeader>{title}</ComponentHeader>
              <Divider />
            </WidthBoundary>
            {component}
            <Divider />
          </ComponentContainer>
        ))}
    </Container>
  );
}

export const query = graphql`
  {
    contentfulPage(slug: { eq: "components" }) {
      ...Page
    }
  }
`;
